@import './../../styles/global.scss';

.logo {
  width: 100%;
  height: 100%;
  &_header {
    max-width: 89px;
    max-height: 24px;
    background: lightgray -13.986px -48.044px / 135.238% 492.785% no-repeat;
    mix-blend-mode: screen;
  }

  &_main {
    background: lightgray -30.885px -106.097px / 135.238% 492.785% no-repeat;
    mix-blend-mode: screen;
    max-width: 197px;
    max-height: 53px;
  }
}

@media screen and (max-width: $breakpointMobile) {
  .logo {
    &_main {
      max-width: 140px;
      max-height: 38px;
    }
  }
}
