@import './../../styles/global.scss';

.header {
  width: 100%;
  height: $headerHeight;
  background-color: $headerBGColor;
  display: flex;
  align-items: center;
  padding: 0 30px;
  box-sizing: border-box;

  &__left {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 25px;
    width: 100%;
    margin-right: auto;
  }

  &__search {
    width: 100%;
  }

  &__right {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    gap: 50px;
  }
}

@media screen and (max-width: $breakpointMiddle) {
  .header {
    &__left {
      gap: 15px;
    }

    &__right {
      gap: 15px;
    }
  }
}
